<template>
  <CommonDataTable
    :tableColumns="tableColumns"
    :getListData="getListData"
    :createRow="createRow"
    :updateRow="updateRow"
    :deleteRow="deleteRow"
  />
</template>

<script>
import { getUserList, createUser, updateUser, deleteUser } from "@/api/manage"
import CommonDataTable from "./components/CommonDataTable.vue"

export default {
  name: "UserInfoTable",
  components: { CommonDataTable },
  data() {
    return {
      /* ******表格配置信息
        Column属性：
          field,label,width,minWidth,expand,fixed,showOverflowTooltip,queryable,queryBoxWidth,queryBoxType,required,
          editable,editType,validator,selectOptions,formatter,cellType,tagOptions
        说明：
          1.width 和 minWidth 只用设置一个，也可以都省略
          2.不可查询时 queryable,queryBoxWidth,queryBoxType 可省略
          3.queryBoxType 默认为 input，非其他类型时可省略
          4.不是必填项时 required 可省略
          5.不可编辑时 editable,editType,validator 可省略
          6.editType 默认为 input，非其他类型时可省略
          7.没有自定义验证规则时，validator 可省略
          8.不用选择框时 selectOptions 可省略
          9.不需要格式化内容时 formatter 可省略
          10.cellType省略时为默认样式，设为tag时单元格按tag标签显示
          11.tag标签对应颜色：""/"success"/"info"/"warning"/"danger" 蓝/绿/灰/黄/红
          12.selectOptions值为数字时不能加引号。例如{ value: "1", label: "男" }，"1"加了引号，
            选择框读取的绑定值为数字1，但找不到匹配标签，显示"1"而不是"男"。
          13.expand 表示是否展开后显示
      ****** */
      tableColumns: [
        { field: "id", label: "ID", minWidth: "100px" },
        {
          field: "userName",
          label: "用户名",
          minWidth: "100px",
          queryable: true,
          queryBoxWidth: "160px",
          queryBoxType: "input",
          required: true,
          editable: true,
        },
        { field: "password", label: "密码", width: "150px", expand: true },
        { field: "salt", label: "盐", width: "70px", expand: true },
        { field: "nickName", label: "昵称", minWidth: "100px", editable: true },
        { field: "realName", label: "真实姓名", width: "90px", expand: true, editable: true },
        {
          field: "gender",
          label: "性别",
          width: "70px",
          expand: true,
          // queryable: true,
          // queryBoxWidth: "80px",
          // queryBoxType: "select",
          editable: true,
          editType: "select",
          selectOptions: [
            { value: 2, label: "男" },
            { value: 1, label: "女" },
            { value: 0, label: "未知" },
          ],
          formatter: this.genderFormatter,
          cellType: "tag",
          tagOptions: { 2: "", 1: "success", 0: "info" },
        },
        {
          field: "birthday",
          label: "生日",
          width: "100px",
          expand: true,
          editable: true,
          editType: "date",
        },
        {
          field: "portrait",
          label: "头像",
          minWidth: "90px",
          editable: true,
          cellType: "avatar",
          additionalFormItem: { type: "image" },
        },
        {
          field: "email",
          label: "邮箱",
          minWidth: "100px",
          editable: true,
          validator: this.emailValidator,
        },
        {
          field: "mobile",
          label: "手机号",
          minWidth: "100px",
          queryable: true,
          queryBoxWidth: "140px",
          queryBoxType: "input",
          editable: true,
          validator: this.mobileValidator,
        },
        { field: "wechat", label: "微信", width: "100px", expand: true, editable: true },
        {
          field: "introduction",
          label: "个人简介",
          width: "140px",
          expand: true,
          editable: true,
          editType: "textarea",
        },
        { field: "loginCount", label: "登录次数", minWidth: "85px" },
        {
          field: "userStatus",
          label: "账户状态",
          minWidth: "85px",
          queryable: true,
          queryBoxWidth: "100px",
          queryBoxType: "select",
          editable: true,
          editType: "switch",
          selectOptions: [
            { value: 1, label: "可用" },
            { value: 0, label: "禁用" },
          ],
          formatter: this.userStatusFormatter,
          cellType: "tag",
          tagOptions: { 1: "success", 0: "danger" },
        },
        {
          field: "isSystem",
          label: "是否系统",
          minWidth: "60px",
          editable: true,
          editType: "select",
          selectOptions: [
            { value: 0, label: "否" },
            { value: 1, label: "是" },
          ],
          formatter: this.isSystemFormatter,
          cellType: "tag",
          tagOptions: { 1: "", 0: "info" },
        },
        {
          field: "isOnline",
          label: "是否在线",
          minWidth: "60px",
          queryable: true,
          queryBoxWidth: "100px",
          queryBoxType: "select",
          selectOptions: [
            { value: 0, label: "离线" },
            { value: 1, label: "在线" },
          ],
          formatter: this.isOnlineFormatter,
          cellType: "tag",
          tagOptions: { 1: "success", 0: "info" },
        },
        { field: "firstVisit", label: "首次登录", minWidth: "100px" },
        { field: "lastVisit", label: "上次登录", minWidth: "100px" },
      ],
    }
  },
  methods: {
    getListData: getUserList,
    createRow: createUser,
    updateRow: updateUser,
    deleteRow: deleteUser,

    /* ****** formatter ****** */
    //全部参数:(row, column, cellValue, index)
    //性别
    genderFormatter(row) {
      return row.gender == 0 ? "未知" : row.gender == 1 ? "女" : "男"
    },
    //用户状态
    userStatusFormatter(row) {
      return row.userStatus == 1 ? "可用" : "禁用"
    },
    isSystemFormatter(row) {
      return row.isSystem == 1 ? "是" : "否"
    },
    //是否在线
    isOnlineFormatter(row) {
      return row.isOnline == 1 ? "是" : "否"
    },
    /* ****** 校验规则 ****** */
    //邮箱校验
    emailValidator(rule, value, callback) {
      const reg = /^[a-zA-Z0-9]+([-_.][A-Za-zd]+)*@([a-zA-Z0-9]+[-.])+[A-Za-zd]{2,5}$/
      setTimeout(() => {
        if (!value || reg.test(value)) {
          callback()
        } else {
          callback(new Error("邮箱格式不正确"))
        }
      }, 100)
    },
    //手机号校验
    mobileValidator(rule, value, callback) {
      // const reg = /^1[34578]\d{9}$/
      const reg = /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/
      setTimeout(() => {
        if (!value || reg.test(value)) {
          callback()
        } else {
          callback(new Error("手机号格式不正确"))
        }
      }, 100)
    },
  },
}
</script>
